import { useEffect, useState } from "react"

const useError = () => {

    const [error, setError] = useState()

    useEffect(() => {
        switch (error) {
            case 'wrong-date':
                setError("A dtum formtuma nem megfelel!")
                break
            case 'no-appointment':
                setError("Erre a napra nem lehet idpontot foglalni!")
                break
            case 'terms-error':
                setError("Nem fogadta el a feltteleket!")
                break
            case 'select-appointment':
                setError("Nem vlasztott idpontot!")
                break
            case 'no-date':
                setError("Nem vlasztott napot!")
                break
            case 'no-service':
                setError("Mg nem vlasztotta ki, hogy milyen szolgltatst venne ignybe!")
                break
            case 'wrong-credentials':
                setError("A megadott nv felttelezheten nem megfelel!")
                break
            case 'wrong-phone':
                setError("A megadott telefonszm nem megfelel")
                break
            case 'internal-error':
                setError("Bels hiba trtnt. Krjk frisstse az oldalt!")
                break
            case 'already-reserved':
                setError("Ez az idpont mr foglalt!")
                break
            default:
                setError("A foglals sikertelen volt! Krjk, ellenrizze az adatait, majd prblja jra!")
        }
    }, [error])

    return [error, setError]

}

export default useError